                    transition: transform 0.5s;
                    &:hover{
                        animation: tn 0.5s linear infinite;
                    }
                    @keyframes tn {
                        0%{
                            transform: rotate(0deg);
                        }100%{
                            transform: rotate(360deg);
                        }
                    }
                }
            }
        }
    }
</style>